@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: 0%;
  padding: 0%;
  font-family: "Poppins", sans-serif;
  background-color: black;
}

header {
  display: flex;
}

.video {
  position: absolute;
  z-index: -1;
  width: 100%;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  align-items: center;
  transition: 0.6s;
  padding: 40px 100px;
}

.logo {
  position: relative;
  font-weight: 700;
  color: white;
  text-decoration: none;
  font-size: 2em;
  text-transform: uppercase;
  letter-spacing: 2px;
  /* animation: fadeIn ease 2s; */
}

/* @keyframes fadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
} */

.header ul {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-animation: fadeInTop ease 2s;
          animation: fadeInTop ease 2s;
}

@-webkit-keyframes fadeInTop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

@keyframes fadeInTop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

.header ul li {
  position: relative;
  list-style: none;
}

.header ul li a {
  position: relative;
  margin: 0 15px;
  text-decoration: none;
  color: white;
  letter-spacing: 2px;
  font-weight: 500px;
  transition: 0.6s;
}

.container {
  display: flex;
  height: 80vh;
}

.containerNest {
  display: flex;
  flex-direction: column;
  margin-left: 150px;
  justify-content: center;
}

.firstsection {
  display: flex;
  flex-direction: column;
  -webkit-animation: fadeIn ease 2s;
          animation: fadeIn ease 2s;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
}

.secondsection {
  display: flex;
  flex-direction: column;
  -webkit-animation: fadeInBot ease 2s;
          animation: fadeInBot ease 2s;
}

@-webkit-keyframes fadeInBot {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

@keyframes fadeInBot {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

.firstsection h1 {
  margin: 0%;
  font-weight: 700;
  color: white;
  text-decoration: none;
  font-size: 4em;
  text-shadow: 2px 2px #000000;
}

.firstsection h1 span {
  color: rgb(90, 240, 30);
}

.firstsection h2 {
  margin: 0%;
  font-weight: 700;
  color: white;
  text-decoration: none;
  font-size: 3em;
  text-shadow: 2px 2px #000000;
}

.secondsection p {
  margin: 0%;
  color: white;
  text-decoration: none;
  font-size: 1.3em;
  text-shadow: 2px 2px #000000;
}

.secondsection p span {
  color: rgb(90, 240, 30);
}

